// inputÏ∞ΩÏóêÏÑú enterÌÇ§ ÏûÖÎ†• Ïãú search Î≤ÑÌäº ÌÅ¥Î¶≠ Í∏∞Îä•
inputEl.addEventListener('keyup', searchEnter);
