// ÎÇ®ÏùÄ Í≤∞Í≥ºÍ∞Ä ÏûàÏùÑ Îïå Î≤ÑÌäº ÎÖ∏Ï∂ú
function renderMoreBtn(totalResults) {
  const total = Math.ceil(Number(totalResults) / 10)
  if (page < total) {
    moreBtnEl.classList.add('active')
  } else {
    moreBtnEl.classList.remove('active')
  }
};

